<template>
  <div :class="[{'apps':listData&&listData.length==0}]" class="bg-gray" id="personal">
    <transition name="slide">
      <div class="con_address">
          <div class="con_a_c2 fixdiv" v-show="fixFlag">
            <a @click="chgTab(1)" >
              <p :class="req_list.queryType==1?'colorYellow':''">全部</p>
            </a>
            <a @click="chgTab(2)">
              <p :class="req_list.queryType==2?'colorYellow':''">待支付</p>
            </a>
            <a @click="chgTab(3)">
              <p :class="req_list.queryType==3?'colorYellow':''">待确认</p>
            </a>
            <a @click="chgTab(4)">
              <p :class="req_list.queryType==4?'colorYellow':''">已确认</p>
            </a>
            <i class="con_a_c2_line"></i>
          </div>
      </div>
    </transition>


    <div id="spring">
      <pull-to :top-load-method="refresh" class="spring-scroller" :on-refresh="refresh" :bottom-load-method="loadmore" :bottomConfig="bottomConfig">
        <header class="per_center m0">
          <div class="pre_title">
            <router-link class="icon-Artboard16" to="/index"></router-link>
          </div>
          <div class="pre_head">
            <div class="pre_head_l">
              <p class="pb18">欢迎您</p>
              <!--1同行 分销商名称 2散客 微信昵称-->
              <h3 v-if="baseMsg.userType==2" class="pl0">{{baseMsg.wxName?baseMsg.wxName:'暂无昵称'}}</h3>
                <h3 v-if="baseMsg.userType==1" class="pl0">{{baseMsg.distributeInfoVO.companyName?baseMsg.distributeInfoVO.companyName:'暂无名称'}}</h3>
            </div>
            <div class="pre_head_r">
              <div class="r_img">
                <img v-if="baseMsg.userType==2&&baseMsg.wxHead" :src="baseMsg.wxHead" alt="">
                <img v-else src="../assets/Art7.png" alt="">
              </div>
              <div class="signOut" @click="isOut=true"><i class="icon-Artboard17"></i>退出登录</div>
            </div>
          </div>
        </header>
        <div class="con_address" >
          <div class="con_a_c2 " v-show="!fixFlag">
            <a @click="chgTab(1)" :class="req_list.queryType==1?'icon-ArtboardCopy7 i-ok':'icon-ArtboardCopy2'">
              <p :class="req_list.queryType==1?'colorYellow':''">全部</p>
            </a>
            <a @click="chgTab(2)" :class="req_list.queryType==2?'icon-Artboard22 i-ok':'icon-Artboard12'">
              <p :class="req_list.queryType==2?'colorYellow':''">待支付</p>
            </a>
            <a @click="chgTab(3)" :class="req_list.queryType==3?'icon-ArtboardCopy6 i-ok':'icon-Artboard14'">
              <p :class="req_list.queryType==3?'colorYellow':''">待确认</p>
            </a>
            <a @click="chgTab(4)" :class="req_list.queryType==4?'icon-ArtboardCopy5 i-ok':'icon-ArtboardCopy3'">
              <p :class="req_list.queryType==4?'colorYellow':''">已确认</p>
            </a>
            <i class="con_a_c2_line"></i>
          </div>
        </div>
        <img v-show="!first" class="first" src="../assets/bg_blank1.jpg" alt="">
        <!-- list -->
        <div v-if="first&&listData.length>0" class="list" v-for="(item,idx) in listData" @click="goOrder(item.orderCode)">
          <div class="order_num order_num1 g_num" :class="idx==listData.length-1?'bbnone':''">
            <div class="order_num_l">
              <div class="item_title clearfix">
                <p class="inline_b fl">
                  <span>{{item.orderStateKey |orderStateKeyFilter}}</span><i class="line_icon">|</i>
                  <span class="colora4ac">{{item.orderCode}}</span>
                </p>
              </div>
              <h4>{{item.pricePlanName}}</h4>
              <div class="order_num_bottom clearfix">
                <div class="fl jing_price">
                  <p>{{item.checkInDate|dateFilter}}入住 -- {{item.checkOutDate|dateFilter}}离店 <i>{{item.roomNight?item.roomNight:' '}}</i>晚
                    <i>{{item.roomQuantity}}</i>间</p>
                  <p>入住人：<span v-for="name in item.guestDTOList">{{name.guestName}}</span></p>
                </div>
              </div>
            </div>
            <p class="head-price"><i class="fzspan c3f">{{item.currency|currencyFilter}} <i
              class="fnt2">{{item.orderSum}}</i></i></p>

            <div class="order_num_r">
              <p class="colorf60">{{item.orderStateShow}}</p>
              <a v-if="item.buttonType==2" href="javascript:void(0)" class="go_pay" @click.stop="goPay(item)">立即支付</a>
              <!--<a href="javascript:void(0)" class="go_pay" @click.stop="goPay(item)">立即支付</a>-->
            </div>
          </div>
        </div>
        <div v-if="listData&&listData.length==0" class="space-notice"><i class="icon-Artboard19"></i>
          <p>暂无{{typeArr[req_list.queryType-1]}}订单</p>
        </div>
      </pull-to>
    </div>


    <!-- add model -->
    <modal :onOk="loginOut" :isShow="isOut" cancetext="取消" oktext="确定" :iShead="false" @close="isOut=false">
      <div class="modal-txt p025">您确定要退出登录吗?</div>
    </modal>
  </div>
</template>

<style scoped>
  .per_center h3 {
    margin-top: -0.05rem;
  }

  .m0 {
    margin: 0 !important;
  }

  .per_center .pre_head {
    margin-bottom: 0;
    padding-bottom: 1.2rem;
  }

  .apps {
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
  }

  .bg-gray {
    background-color: #F2F4F5;
    height: 100%;
  }
</style>
<script>
  import {currencyFomate,getnotice} from '../common'
  import PullTo from 'vue-pull-to'
  export default {
    components: {
      PullTo
    },
    data () {
      return {
        scroll:'',
        first: false,
        isOut: false,
	      fixFlag: false,
        bottomConfig:{
          pullText: '上拉加载',
          triggerText: '释放更新',
          loadingText: '加载中...',
          doneText: '加载完成',
          failText: '加载失败',
          loadedStayTime: 400,
          stayDistance: 50,
          triggerDistance: 70
        },
        baseMsg: '',
        isfix: false,
        typeArr: ['', '待支付', '待确认', '已确认'],
        req_list: {//
          "queryType": 1,//1查全部订单，2待支付，3待确认,4、已确认
          "pageNo": "1",
          "pageSize": "5"
        },
        /*res*/
        model: '',
        listData: [],
      }
    },
    filters: {
      dateFilter(date){
        if (date) {
          var dateArr = date.split('-');
          var dateStr = dateArr[1] + '/' + dateArr[2]
          return dateStr
        }
      },
      orderStateKeyFilter(type){
        var MethodArr = ['新建', '处理中', '已确认', '已取消']
        if (type) return MethodArr[type - 1]
      },
      currencyFilter(type){
        return currencyFomate(type);
      }
    },
  watch: {
    $route(val){
        //console.log(val);
      }
    },
    created: function () {
      var _t = this;
      setTimeout(function () {
        _t.first = true;
      }, 1000)
      this.baseInfo();

    },
    destroyed () {
      var _t = this;
      setTimeout(function(){
        var app = document.querySelector("#personal");
          if(app){
            function fixCall() {
              if (_t.listData.length == 0) return;
              var list = $(".list").eq(0);
              if(list){
                var h = list.offset().top;
                h > 50 ? (_t.fixFlag = false) : (_t.fixFlag = true);
              }
            }
            app.removeEventListener("touchmove",fixCall);
            app.removeEventListener("touchend", fixCall);
          }
      },500)

    },
    mounted: function () {
      var _t = this;
      lineAnimate();
      singout();
      //scrollfix();
      function scrollfix(){
        var app = document.querySelector("#personal");
        if(app){
          function fixCall() {
            if (_t.listData.length == 0) return;
            var list = $(".list").eq(0);
            if(list){
              var h = list.offset().top;
              h > 50 ? (_t.fixFlag = false) : (_t.fixFlag = true);
            }
          }
          app.removeEventListener("touchmove",fixCall);
          app.removeEventListener("touchend", fixCall);
          app.addEventListener("touchmove",fixCall);
          app.addEventListener("touchend", fixCall);
        }
      }
      function lineAnimate() {
        $('.con_a_c2 a').click(function () {
          var _width = $(this).width();
          var _index = $(this).index();
          var w = $(window).width();
          var lefts = $(this).offset().left;
          $(".con_a_c2").find(".con_a_c2_line").animate({ left: lefts, width: _width }, 200);
          $('body').scrollTop(0);
        })
      }
      function singout() {
        $('.r_img').on('click', function (e) {
          e.stopPropagation()
          if (_t.baseMsg.userType == 1) {
            //散客不能登出
            $('body').append('<div class="signOutBg"></div>');
            $('.signOut').show();
          }
        })
        $('body').on('click', function () {
          $('.signOutBg').remove();
          $('.signOut').hide();
        })
      }
    },

    methods: {
      //temp
      menu(){
        this.scroll = document.body.scrollTop;
        var list = $(".con_a_c2");
        var h = list.offset().top;
        //console.log(this.scroll,'监听到滚动事件',h);
      },

      refresh(loaded) {
        var _t = this;
        _t.listData = [];
        _t.req_list.pageNo = 1;
        _t.bottomConfig.pullText = '上拉加载'
        _t.bottomConfig.doneText = '加载完成'
        _t.Qlist(loaded);
      },

      loadmore(loaded){
        var _t = this;
        _t.req_list.pageNo++;
        _t.Qlist(loaded);
      },

      /*req-res*/
      baseInfo(){
        var _t = this;
        this.$ajax.post('/common/queryUserInfo.shtml').then(function (res) {
          if (res.data.result == 1) {
            _t.baseMsg = res.data.model;
            var title = res.data.model.hotelName
            document.title = title
            if (_t.baseMsg.userType == 2 && !_t.baseMsg.memberInfoVO.mobile) {
              _t.$router.push('/personalPre');
            }
            if (_t.baseMsg.userType == 2 && _t.baseMsg.memberInfoVO.mobile || _t.baseMsg.userType == 1) {
              _t.Qlist();
            }
          }
        }).catch(function (err) {

        });
      },

