          }
          if (res.data.result == 1) {
            _t.model = res.data.model;
            if (res.data.model.itemList) {
              _t.listData = _t.listData.concat(_t.model.itemList);
              if(loaded) loaded('done');
            } else {
              _t.listData = list;
            }
            if(_t.model.totalPage == _t.req_list.pageNo){
              _t.bottomConfig.pullText = '已经没有了'
              _t.bottomConfig.doneText = '已经没有了'
            }
          }
        }
        //return;
        _t.$ajax.post('/hotel/order/queryOrderList', data).then(function (res) {
          if (res.data.result == 1) {
            _t.model = res.data.model;
            if (res.data.model.itemList) {
              _t.listData = _t.listData.concat(_t.model.itemList);
              loaded('done');
            } else {
              _t.listData = list;
            }
            if(_t.model.totalPage == _t.req_list.pageNo){
              _t.bottomConfig.pullText = '已经没有了'
              _t.bottomConfig.doneText = '已经没有了'
            }
          }
        }).catch(function (err) {

        });
      },

      pageSpring(){
        var _t = this;
        myscroll = new iScroll("spring", {
          onScrollMove: function () {
            this.y > 50 ? (pushFalg = true) : (pushFalg = false);
            this.y < this.maxScrollY ? pullFalg = true : pullFalg = false;
          },
          onScrollEnd: function () {
            if (pushFalg)  _t.Qlist();
            if (pullFalg) {
              _t.req_list.pageNo++;
              _t.Qlist()
            };
          },
        });
      },

      chgTab(n){
        if (this.req_list.queryType != n) {
          this.listData = [];
          this.req_list.queryType = n;
          this.req_list.pageNo = 1;
          this.Qlist();
        }
      },

      goOrder(orderCode){
        var orderPath = '/Order/' + orderCode + '/0';
        this.$router.push(orderPath);
      },

      loginOut(){
        var _t = this;
        this.$ajax.post('/common/b2b/logout.shtml').then(function (res) {
          if (res.data.result == 1) {
            getnotice('登出成功');
            setTimeout(function () {
              _t.$router.push('/login')
            }, 800)
          }
        }).catch(function (err) {

        });
      },

      /*others*/
      goPay(item) {
        var host = window.location.host;
        window.location.href = 'http://' + host + '/miceMobile/payPage.html?orderCode='+item.orderCode+'&token='+item.token;
      },
    }
  }
</script>



